// src/app/layout.js
import { auth, signOut } from "@/auth"
import { DynamicBreadcrumb } from "@/components/navigation/dynamic-breadcrumb"
import { Button } from "@/components/ui/button"
import { Separator } from "@/components/ui/separator"
import { SidebarInset, SidebarTrigger } from "@/components/ui/sidebar"
import { Toaster } from "@/components/ui/sonner"
import { ThemeProvider } from "@/components/theme-provider"
import { ThemeToggle } from "@/components/theme-toggle"
import { AppSidebar, AppSidebarProvider } from "@/features/navigation"
import { getDistinctLineIds } from "@/features/line-dashboard/api/get-line-ids"
import "./globals.css"

export const metadata = {
  title: "Create Next App",
  description: "Generated by create next app",
}

export default async function RootLayout({ children }) {
  const session = await auth()
  const isAuthenticated = !!session?.user

  let lineOptions = []

  if (isAuthenticated) {
    try {
      lineOptions = await getDistinctLineIds()
    } catch {
      lineOptions = []
    }
  }

  async function handleSignOut() {
    "use server"
    await signOut({ redirectTo: "/auth/signin" })
  }

  return (
    <html lang="en" suppressHydrationWarning>
      <body>
        <ThemeProvider>
          {isAuthenticated ? (
            <AppSidebarProvider>
              <AppSidebar lineOptions={lineOptions} />
              <SidebarInset>
                <header
                  className="flex h-14 shrink-0 items-center justify-between gap-2 px-4 transition-[width,height] ease-linear group-has-data-[collapsible=icon]/sidebar-wrapper:h-12"
                >
                  <div className="flex items-center gap-2">
                    <SidebarTrigger className="-ml-1" />
                    <Separator orientation="vertical" className="mr-2 data-[orientation=vertical]:h-4" />
                    <DynamicBreadcrumb />
                  </div>
                  <div className="flex items-center gap-3">
                    {session?.user?.name ? (
                      <span className="hidden text-sm font-medium text-muted-foreground sm:inline-flex">
                        {session.user.name}
                      </span>
                    ) : null}
                    <ThemeToggle />
                    <form action={handleSignOut}>
                      <Button size="sm" variant="outline" type="submit">
                        Sign out
                      </Button>
                    </form>
                  </div>
                </header>
                <main className="flex-1 min-h-0 min-w-0 p-2 pt-0 overflow-hidden">
                  <div className="h-full overflow-hidden">
                    {children}
                  </div>
                </main>
              </SidebarInset>
            </AppSidebarProvider>
          ) : (
            <div className="min-h-screen">{children}</div>
          )}
          <Toaster />
        </ThemeProvider>
      </body>
    </html>
  )
}
